  width: 5px;

}



.no-scrollbar::-webkit-scrollbar {
  display: none;
}


@keyframes hareket {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(10px);
  }

  100% {
    transform: translateX(0);
  }
}

/* rnein, bir div'e bu animasyonu eklemek iin: */
.animate-x {

  animation: hareket 1.5s infinite;
  /* 1 saniyede bir kez tekrarlanacak */
}


